
'use client';

import { useState, useEffect, useCallback } from 'react';
import type { Nft } from '@/lib/types';
import placeholderData from '@/lib/placeholder-images.json';

const getInitialNfts = (): Nft[] => [
  {
    hash: '0x123abc',
    title: 'The Silent Observer',
    description: 'A short story about a forgotten statue in a bustling city park.',
    content: 'In the heart of the city, amidst the ceaseless dance of life, stood a silent observer. Cast in bronze, weathered by time, it watched generations come and go. Children played at its feet, lovers carved their initials into the bench nearby, and the weary found a moment of solace under its gaze. It held a thousand stories, yet spoke none.',
    owners: [{ address: '0xcreator1', timestamp: Date.now() - 1000 * 60 * 60 * 24 * 7 }],
    imageUrl: placeholderData.placeholderImages[0].imageUrl,
    imageHint: placeholderData.placeholderImages[0].imageHint
  },
  {
    hash: '0x456def',
    title: 'Cosmic Lullaby',
    description: 'A poem dedicated to the quiet vastness of space.',
    content: 'Hush, little star,\nIn your cradle of night.\nThe moon sings a tune,\nof ethereal light.\nDrift through the cosmos,\na shimmering dream,\nAcross the dark river,\nof a silent stream.',
    owners: [{ address: '0xcreator2', timestamp: Date.now() - 1000 * 60 * 60 * 24 * 5 }],
    imageUrl: placeholderData.placeholderImages[3].imageUrl,
    imageHint: placeholderData.placeholderImages[3].imageHint
  },
  {
    hash: '0x789ghi',
    title: 'Cyber-Ronin: Panel 1',
    description: 'The first panel of an upcoming cyberpunk comic series.',
    content: 'The neon signs of Neo-Kyoto bled into the rain-slicked streets. Kaito drew his katana, its plasma edge humming in the oppressive humidity. His target was just across the plaza, a corporate overlord who thought he was above the street\'s justice. He was wrong.',
    owners: [{ address: '0xcreator1', timestamp: Date.now() - 1000 * 60 * 60 * 24 * 3 }],
    imageUrl: placeholderData.placeholderImages[5].imageUrl,
    imageHint: placeholderData.placeholderImages[5].imageHint
  },
  {
    hash: '0x987jkl',
    title: 'A Writer\'s Solitude',
    description: 'On the profound loneliness and joy of creating worlds from words.',
    content: 'There is a unique solitude in writing. It is a quiet room, a blank page, a flickering cursor. It is a conversation with ghosts and a dance with shadows. In this self-imposed exile, worlds are born, and the soul finds its voice. It is the loneliest and most crowded place to be.',
    owners: [{ address: '0xcreator3', timestamp: Date.now() - 1000 * 60 * 60 * 24 * 2 }],
    imageUrl: placeholderData.placeholderImages[1].imageUrl,
    imageHint: placeholderData.placeholderImages[1].imageHint
  },
];

const NFT_STORAGE_KEY = 'storyverse_nfts';

export function useNftStore() {
  const [nfts, setNfts] = useState<Nft[]>([]);
  const [isLoaded, setIsLoaded] = useState(false);

  useEffect(() => {
    let initialNfts: Nft[] = [];
    try {
      const storedNfts = localStorage.getItem(NFT_STORAGE_KEY);
      if (storedNfts) {
        initialNfts = JSON.parse(storedNfts);
      } else {
        initialNfts = getInitialNfts();
        localStorage.setItem(NFT_STORAGE_KEY, JSON.stringify(initialNfts));
      }
    } catch (error) {
      console.error('Failed to access localStorage:', error);
      initialNfts = getInitialNfts();
    }
    setNfts(initialNfts);
    setIsLoaded(true);
  }, []);

  const updateLocalStorage = (updatedNfts: Nft[]) => {
    try {
      localStorage.setItem(NFT_STORAGE_KEY, JSON.stringify(updatedNfts));
      setNfts(updatedNfts);
    } catch (error) {
      console.error('Failed to save to localStorage:', error);
    }
  };

  const addNft = useCallback((newNftData: Omit<Nft, 'hash' | 'imageUrl' | 'imageHint' | 'owners'> & { owner: string }): Nft => {
    const randomImage = placeholderData.placeholderImages[Math.floor(Math.random() * placeholderData.placeholderImages.length)];
    const newNft: Nft = {
      title: newNftData.title,
      description: newNftData.description,
      content: newNftData.content,
      hash: `0x${Array.from({ length: 10 }, () => '0123456789abcdef'[Math.floor(Math.random() * 16)]).join('')}`,
      imageUrl: randomImage.imageUrl,
      imageHint: randomImage.imageHint,
      owners: [{ address: newNftData.owner, timestamp: Date.now() }],
    };
    const updatedNfts = [newNft, ...nfts];
    updateLocalStorage(updatedNfts);
    return newNft;
  }, [nfts]);

  const transferNft = useCallback((hash: string, newOwnerAddress: string): boolean => {
    const nftToTransfer = nfts.find(nft => nft.hash === hash);
    if (!nftToTransfer) return false;

    const updatedNfts = nfts.map(nft => 
      nft.hash === hash 
        ? { ...nft, owners: [...nft.owners, { address: newOwnerAddress, timestamp: Date.now() }] } 
        : nft
    );
    updateLocalStorage(updatedNfts);
    return true;
  }, [nfts]);
  
  const getNftByHash = useCallback((hash: string): Nft | undefined => {
    return nfts.find(nft => nft.hash === hash);
  }, [nfts]);


  return { nfts, isLoaded, addNft, transferNft, getNftByHash };
}
